import React, { Fragment } from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Link } from "gatsby"
import ProjectSelection from "../components/project-selection";

// with domain experts such as 

let descriptionText = <div>
    <p>Here is a list of projects I have worked on for courses, independent ideas, competitions, and research investigation. I am privileged to have <span className="has-text-weight-semibold">collaborated with software developers, visual designers, occupational therapists, public health researchers, physicians, caregivers, and patients</span>, to co-create a better user experience.
    </p>
</div>;



const Projects = () => (
    <Layout>
        <SEO 
            title="Projects" 
            description="Collaborate with visual designers, occupational therapists, epidemiologists, physicians, caregivers, and patients, to co-create a better user experience."
            keywords={[`Pei-Yao Hung`, `Human-Computer Interaction`, `User Experience`, `Researcher`, `Project`]} 
        />
        <h1 className="title is-3 is-spaced is-size-4-mobile">Projects</h1>
        <ProjectSelection description={descriptionText} />
    </Layout>
)

export default Projects
